<template>
    <v-list-item class="pa-0">
      <v-list-item-avatar class="ma-0 mx-1" size="30">
        <v-img :src="getImageSrc($attrs.item.profileImage)"></v-img>
      </v-list-item-avatar>
      <v-list-item-content>

      <span class="d-flex flex-column">

      <span v-html="text"></span>
      <small> {{ $attrs.item.role }}  {{department}}</small>
      </span>
      </v-list-item-content>
    </v-list-item>
  
</template>

<script>
export default {
    name: 'AutoCompleteUserItem',
    computed: {
      text () {
        const { searchInput } = this.$attrs.parent
        const { displayName } = this.$attrs.item
        return searchInput ? this.maskValue(searchInput, displayName) : displayName
      },
       department () {
        const { department } = this.$attrs.item
        return (department != null && department && department.name) ? `(${department.name})` : '' 
      },
    }
}
</script>

