 
 
export default {
    stringToJson(str) {
        try {
            return JSON.parse(str)
        } catch (error) {
            return str
        }
    }, 
    jsonToString (object) {
        try {
            return JSON.stringify(object)
        } catch (error) {
            return object
        }
    },

    // add leading zero
    pad(number, length=2) {
        var str = '' + number;
        while (str.length < length) {
            str = '0' + str;
        }
        return str;
    },

    money (amount, currency = 'USD') {
        return new Intl.NumberFormat('en-US', { style: 'currency', currency }).format(amount)
    },
    get today() {
        let today = new Date();
        today.setHours(0, 0, 0, 0);
        return today
    },
    get first_day_of_the_month() {
        const today = new Date()
        return new Date(today.getFullYear(), today.getMonth(), 1)
    },
    get todayString() {
        const today = this.today
        const { pad } = this
        return [`${today.getFullYear()}-${pad(today.getMonth() + 1, 2)}-${pad(today.getDate(), 2)}`, `${today.getFullYear()}-${pad(today.getMonth() + 1, 2)}-${pad(today.getDate(), 2)}`]
    },
    get last7daysString() {
        const today = this.today
        const prev7days = new Date(this.today.getTime())
        prev7days.setDate(prev7days.getDate() - 7)
        
        const { pad } = this
        return [`${prev7days.getFullYear()}-${pad(prev7days.getMonth() + 1, 2)}-${pad(prev7days.getDate(), 2)}`, `${today.getFullYear()}-${pad(today.getMonth() + 1, 2)}-${pad(today.getDate(), 2)}`]
    },
    get currentDateRange() {
        const today = this.today 
        const { pad } = this
        return [`${today.getFullYear()}-${pad(today.getMonth() + 1, 2)}-01`, `${today.getFullYear()}-${pad(today.getMonth() + 1, 2)}-${pad(today.getDate(), 2)}`]
    },
    findColorBetween(left, right, percentage) {
        const newColor = {};
        const components = ["r", "g", "b"];
        for (var i = 0; i < components.length; i++) {
            let c = components[i];
            newColor[c] = Math.round(left[c] + (right[c] - left[c]) * percentage / 100);
        }
        return newColor
    },
    getNestedValue(obj, path, fallback) {
        const last = path.length - 1

        if (last < 0) return obj === undefined ? fallback : obj

        for (let i = 0; i < last; i++) {
            if (obj == null) {
                return fallback
            }
            obj = obj[path[i]]
        }

        if (obj == null) return fallback

        return obj[path[last]] === undefined ? fallback : obj[path[last]]
    },
    getObjectValueByPath(obj, path, fallback = null) {
        // credit: http://stackoverflow.com/questions/6491463/accessing-nested-javascript-objects-with-string-key#comment55278413_6491621
        if (obj == null || !path || typeof path !== 'string') return fallback
        if (obj[path] !== undefined) return obj[path]
        path = path.replace(/\[(\w+)\]/g, '.$1') // convert indexes to properties
        path = path.replace(/^\./, '') // strip a leading dot
        return this.getNestedValue(obj, path.split('.'), fallback)
    },
    filters: {
        getType (field) {
            if (field.type)
                return field.type
            else switch (field.input.toLowerCase()) {
                case 'text':
                    return 'search';
                case 'range':
                    return 'between';
                default:
                    return 'eq'
            }
        },
        getValue(value, type = 'eq', options = 'contain') {
            switch (type.toLowerCase()) {
                case 'between':
                    return Array.isArray(value) ? value.join('|') : value
                case 'search':
                    switch (options.toLowerCase()) {
                        case 'start': // start with
                            return `^${value}`
                        case 'end': // end with
                            return `$${value}`
                        case 'contain': // contain
                        default:
                            return `~${value}` 
                    }
                case 'gt': //greater than
                    return `>${value}` 
                case 'lt': //less than
                    return `<${value}` 
                case 'gte': //greater than or equal to
                    return `>=${value}`
                case 'lte': //less than or equal to
                    return `<=${value}` 
                case 'ne': // unequal
                    return `!${value}` 
                case 'eq': 
                default: // equal
                    return value
            }


        },
        setValue(value, type = 'eq') {
            if (!value) return value
            switch (type.toLowerCase()) {
                case 'between':
                    return value.split('|')
                case 'search':
                case 'gt': //greater than
                case 'lt': //less than
                case 'ne': // unequal
                    if (['~', '^', '$', '>', '<', '!'].indexOf(value.substr(0,1)) !== -1) 
                        return value.substr(1)
                    else
                        return value
                    
                case 'gte': //greater than or equal to
                case 'lte': //less than or equal to
                    if (['>=', '<='].indexOf(value.substr(0, 2)) !== -1)
                        return value.substr(2)
                    else
                        return value
                case 'eq':
                default: // equal
                    return value
            }


        } 
    }

    
}