<template>

<v-autocomplete
  ref="autocomplete"
  v-on="$listeners"
  :items="items"
  :loading="$attrs.loading || loading"
  @focus="serverSearch"
  :search-input.sync="searchInput"
  :no-filter="Boolean(collection)"
  v-bind="$attrs"
  >
  <!-- return-object -->
    <template  v-for="(slot, index) in slots" v-slot:[slot]="options">
      <span :key="index" >
        <component :is="`slot-${template}-${slot}`" v-bind="options" ></component>
      </span>
    </template>
  </v-autocomplete> 

</template>

<script>
import $ from 'jquery'
import debounce from 'debounce'
export default {
  name: 'AppAutocomplete',
  props: {
    collection: String,
    serverValue: Boolean,
    query: Object,
    searchIncludes: {
      type: Boolean,
      default: true
    }
    
  },
  data: () => ({
    loading: false,
    initValue: null,
    searchInput: '',
    serverItems: []
  }),
  computed:{
    items () {
      return this.serverItems.length ? this.serverItems : this.$attrs.items
    },
    template () {
      return this.$attrs.template || 'default'
    },
    templateFiles () {
      return  require.context('./autocomplete/', true, /\.vue$/).keys() 
    },
    templates () {
      return this.template ? this.templateFiles
                            // filter the relavent files 
                            .filter(path => path.split('/')[1] == this.template)
                            //remove leading dot
                            .map(f => f.slice(1)) : []
    },
    slots () {
      return this.templates.map(file => {
        const splited = file.split('/')
        return splited[splited.length-1].split('.')[0].toLowerCase()
      })
    },
    //search
    searchStrQuery () {
      return `${ this.searchIncludes ? '~' : '^'}${typeof this.searchInput  == 'string' ? this.searchInput : ''}`
    },
    searchQuery () {
      const { itemText, searchFields } = this.$attrs
      const { query } = this
      const serverQuery = {...query}
      if (Array.isArray(searchFields) && searchFields.length) {
          serverQuery.orFields = [itemText]
          serverQuery.orValues = [this.searchStrQuery]
          searchFields.forEach(field => {
            serverQuery.orFields.push(field)
            serverQuery.orValues.push(this.searchStrQuery)
          })

      } else {
        serverQuery[itemText] = this.searchStrQuery
      }
      return serverQuery
    }
   
  },
  watch: {
    searchInput: {
      handler (value) { 
        const { isSearching } = this.$refs.autocomplete
        if (value != null && isSearching)
          debounce(this.serverSearch, 500)(value) 
      }
    },
    
  },
  methods: {
    async serverSearch () {
    
      
      if (!this.collection) return
      

      const options = {
        collection: this.collection + '?' + $.param(this.searchQuery) 
      }
      
      this.loading = true 
      const res = await this.get(options)
      this.loading = false 
      
      this.serverItems = res.data.docs || res.data
      
      // avoid removing selected value if not in the items 
      if (this.initValue && !this.serverItems.find(item => item._id == this.initValue._id))
        this.serverItems.unshift(this.initValue)
     
    },
    
  },
  async created () {
    if (this.$attrs.value && this.collection) {
 
      // set initial value
      if (this.serverValue) { // from the server value
        this.loading = true
        this.$attrs.disabled = true
        const item = await this.getItem({ collection:this.collection, id:this.$attrs.value })
        this.loading = false
        this.$attrs.disabled = false
        this.initValue = item.data
      } else  // object value
        this.initValue = this.$attrs.value


      this.serverItems.unshift(this.initValue)
    
    }
    // set dynamic slots
    this.slots.forEach( (slot, index) => {
      this.$options.components[`slot-${this.template}-${slot}`] =  () => import(`./autocomplete${this.templates[index]}`)
    });
  }
}
</script>

