<template>
  <span>
    <v-list-item-avatar class="ma-0">
      <country-flag :country="$attrs.item.code"></country-flag>
    </v-list-item-avatar>
    <span v-html="text"></span>
  </span>
  
</template>

<script>
export default {
    name: 'AutoCompleteCountryItem',
    computed: {
      text () {
        const { searchInput } = this.$attrs.parent
        const { name } = this.$attrs.item
        
        return searchInput ? this.maskValue(searchInput, name) : name
      }
    }
}
</script>

