/* eslint no-empty-pattern: ["off"] */
import axios from "axios";
import config from '../../config'
import utility from './../../utility'
const { jsonToString, stringToJson } = utility
const { baseURL } = config 
const getInstance = () => {
    const instance = axios.create({
        baseURL 
      });
    return instance
}
function stripHtml(html)
{
   var tmp = document.createElement("DIV");
   tmp.innerHTML = html;
   return tmp.textContent || tmp.innerText || "";
}
export default {
    namespaced: true,
    state: {
        user: stringToJson(localStorage.getItem('user')) || null,
        viewUser: stringToJson(localStorage.getItem('viewUser')) || null,
        token: localStorage.getItem('token') || null,
        // menu: JSON.parse(localStorage.getItem('menu')) || [],
    },
    mutations: {
        user (state, payload) {
            localStorage.setItem('user', jsonToString(payload))
            state.user = payload
        },
        viewUser(state, payload) {
            const vu = jsonToString(payload)
            if (vu)
                localStorage.setItem('viewUser', vu)
            else
                localStorage.removeItem('viewUser', vu)

            state.viewUser = payload
        },
        token (state, payload) {
            localStorage.setItem('token', payload)
            state.token = payload
        },
        // menu(state, payload) {
        //     if (payload)
        //         localStorage.setItem('menu', JSON.stringify(payload))
        //     state.menu = payload
        // },
    },
    getters: {
        isAuthenticated: state => !!state.token,
        isViewUser: state => !!state.viewUser,
        getUser: (state, getters) => {
            return getters.isViewUser ? state.viewUser : state.user
        }
    },
    actions: {
        async register ({ commit }, payload) {
            const { username, password } = payload
            const instance = getInstance()
            const res = await instance.post('/auth/register', { username, password })
            const { token, user, menu } = res.data
            
            if (menu)
                user.menu = menu
            
            commit('token', token)
            commit('user', user)
            // commit('menu', res.data.menu)
            
        },
        async login ({ commit }, payload) {
            const { username, password } = payload
            const instance = getInstance()
       
            const res = await instance.post('/auth/login', { username, password })
            const { token, user, menu } = res.data
            if (menu)
                user.menu = menu

            commit('token', token)
            commit('user', user)
            // commit('menu', res.data.menu)
        },
        async logout ({ commit }) {
          
            await commit('token', null)
            await commit('viewUser', null)
            await commit('user', null)
            localStorage.removeItem('user')
            localStorage.removeItem('viewUser')
            localStorage.removeItem('token')
        },
        async dark ({  commit, state }, payload) {
            state.user.dark_theme = payload
            const instance = getInstance()

            const res = await instance.put('/auth/dark', { id: state.user._id, dark_theme:payload}).catch(e => commit('error', stripHtml(e.response ? e.response.data : e), { root: true }))
            if (res.status == 200)
            commit('user', {...state.user})    
 
        },
    }

}