import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import store from './../store'

Vue.use(VueRouter)





  const routes = [
  {
    path: '/login',
    name: 'Login',
    
    component: () => import('../views/Login.vue')
  },
  {
    path: '/screen',
    name: 'Screen',

    component: () => import('../views/Screen.vue')
  },
  {
    path: '/screen/:data',
    name: 'ScreenView',
    component: () => import('../views/ScreenView.vue')
  },
  {
    path: '/',
    component: Home,
    meta: {
      requierAuth: true
    },
    children: [
      {
        path: '/',
        name: 'Dashboard',
        component: () => import('../views/Dashboard.vue'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/reports',
        name: 'Reports',
        component: () => import('../views/reports/Index.vue'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/link',
        name: 'Reports',
        component: () => import('../views/Link.vue'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/sales',
        name: 'Sales',
        component: () => import('../views/Sales'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/report_builder',
        name: 'ReportBuilder',
        component: () => import('../views/ReportBuilder.vue'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/404',
        name: '404',
        component: () => import('../views/404.vue'),
       
      },
      {
        path: '/retention',
        name: 'Retention',
        component: () => import('../views/Retention'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/ftd',
        name: 'FTD',
        component: () => import('../views/FTD'),
        meta: {
          requierAuth: true
        },
      },
      
      {
        path: '/redeposit',
        name: 'Redeposit',
        component: () => import('../views/Redeposit'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/report',
        name: 'Report',
        component: () => import('../views/Report.vue'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/report2',
        name: 'Report2',
        component: () => import('../views/ReportNew.vue'),
        meta: {
          requierAuth: true
        },
      },
      {
        path: '/:collection',
        name: 'Items',
        component: () => import('../views/items/Index.vue'),
        // component: () => import('../views/Items.vue'),
        meta: {
          requierAuth: true
        },
      },
      // {
      //   path: '/:collection/new',
      //   name: 'Item',
      //   component: () => import('../views/item/Index.vue'),
      //   // component: () => import('../views/Item.vue'),
      //   meta: {
      //     requierAuth: true
      //   },
      // },
      {
        path: '/:collection/:id',
        name: 'Item',
        component: () => import('../views/item/Index.vue'),
        // component: () => import('../views/Item.vue'),
        meta: {
          requierAuth: true
        },
      } 
    ]
  }
 
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})


// const adminRole = ['admin', 'manager']
router.beforeEach((to, from, next) => {
  const { requierAuth } = to.meta
  if(requierAuth && !store.getters['auth/isAuthenticated'])
    return next('/login')
  if(to.name == 'Login' && store.getters['auth/isAuthenticated'])
    return next('/')
 
    // access controle
    if (to.params.collection) {
      const { rights } = store.getters['auth/getUser']
     
      if (!rights || (rights && !rights.length))
        return next()
      else {
        let permissions = rights.find(el => el.collection == to.params.collection)
        if (!permissions)
          return next()
        else {
          
          
          if (to.params.id) {  // item view
            if (to.params.id.toLowerCase() == 'new') { // new item
              
              if (permissions.write.value)
                return next()
              else
                return next('/404') 
            } else { // update item
              
              if (permissions.update.value)
                return next()
              else
                return next('/404')
            }

          } else {  // table view
            
            if (permissions.read.value )
              return next()
            else
              return next('/404') 
          }
          
        } 

        
      }
    }
 


  return next()
})
export default router
