<template>
  <span>
    <v-list-item-content>
      <v-list-item-title v-html="name">
      </v-list-item-title>
      <v-list-item-subtitle v-html="tpc">
      </v-list-item-subtitle>
    </v-list-item-content>
  </span>
  
  
</template>

<script>
export default {
    name: 'AutoCompleteCustomerItem',
    computed: {
      name () {
        const { searchInput } = this.$attrs.parent
        const { name } = this.$attrs.item
        
        return searchInput ? this.maskValue(searchInput, name) : name
      },
      tpc () {
        const { searchInput } = this.$attrs.parent
        const { tpc } = this.$attrs.item
        
        return searchInput ? this.maskValue(searchInput, tpc) : tpc
      }
    }
}
</script>

