<template>
  <v-app>
    <v-navigation-drawer app  v-model="drawer" >
      <v-card flat  tile v-if="user">
        <v-card-text class="text-center">
          <v-layout d-flex column> 
            <v-flex>
              <v-avatar size="130" >
                <v-img :src="getImageSrc(user.profileImage)"></v-img>
              </v-avatar>
            </v-flex>
            <v-flex class="subtitle-1 font-weight-bold">
              {{ user.displayName }}
            </v-flex>
            <v-flex class="caption">
              {{ user.role }}
            </v-flex>
          </v-layout>
        </v-card-text>
      </v-card>
      <v-divider></v-divider>
      <v-list v-if="menu">
       <template v-for="(item, index) in menu.menu_item"   >
        <v-list-item    :key="index" :to="item.path">
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ item.name }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
       </template>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar app color="primary" >
      <v-app-bar-nav-icon
        @click.stop="drawer = !drawer"
      ></v-app-bar-nav-icon>
      <v-spacer></v-spacer>
      
      <v-menu
        min-width="335"
        :close-on-content-click="false"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-list class="ma-0 pa-0 transparent" >
            <v-list-item v-bind="attrs" v-on="on" class="ma-0 pa-0 transparent">
              <v-list-item-avatar size="40">
                  <v-img :src="getImageSrc(user.profileImage)"></v-img>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title>
                  {{ user.displayName }}
                </v-list-item-title>
                <v-list-item-subtitle>
                  {{ user.role }} {{userDepartment}}
                </v-list-item-subtitle>
              </v-list-item-content>
              <v-list-item-icon>
                <v-icon>mdi-chevron-down</v-icon>
              </v-list-item-icon>
            </v-list-item>
          </v-list>
        </template>

        <v-list>
          <v-list-item>
              <v-list-item-avatar size="40">
                  <v-img :src="getImageSrc(currentUser.profileImage)"></v-img>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title>
                  {{ currentUser.displayName }}
                </v-list-item-title>
                <v-list-item-subtitle>
                  {{ currentUser.role }} {{currentUserDepartment}}
                </v-list-item-subtitle>
              </v-list-item-content>
               <v-list-item-icon>
                 <v-btn text @click="logout">
                  <v-icon>mdi-logout</v-icon>
                  Logout
                 </v-btn>
              </v-list-item-icon>
            </v-list-item>
            <v-divider class="my-3" v-if="currentUser.role == 'admin' || currentUser.role == 'owner'"></v-divider>
            <v-list-item v-if="currentUser.role == 'admin' || currentUser.role == 'owner'">
              <app-autocomplete
              label="View app as"
              outlined
              dense
              collection="user"
              template="user"
              itemText="displayName"
              return-object
              clearable
              v-model="viewUser"
              :loading="viewAppUser.loading"
              :disabled="viewAppUser.loading"
            ></app-autocomplete>
            </v-list-item>
        </v-list>
      </v-menu>
     
      

      <!-- -->
    </v-app-bar>

    <!-- Sizes your content based upon application components -->
    <v-main :class="mainClass">
      <!-- Provides the application the proper gutter -->
      <v-container fluid>
        <!-- If using vue-router -->
        <router-view></router-view>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import AppAutocomplete from './../components/Autocomplete'

export default {
  name: 'Home',
  components: {
    AppAutocomplete
  },
  data: () => ({
    drawer: null,
    drawerList: [],
    viewAppUser: {
      loading: false
    },
  }),
  computed: {
    
    menu () {
      // const link = {
      //   name: 'Link',
      //   icon: 'mdi-web',
      //   path: '/link',
      // }
      // if (!this.user) return []
      const m = this.user.menu || []
      // m.menu_item.unshift(link)
      return m 
    },
    currentUser () {
      return  this.$store.state.auth.user
    },
    currentUserDepartment() {
        if (!this.currentUser) return ''
        const { department } = this.currentUser
        return (department != null && department && department.name) ? `(${department.name})` : ''
    },
    viewUser: {
      get () {
        return this.$store.state.auth.viewUser
      },
      async set (user) {
        let selectedUser = user
        if (user) {
          
          this.viewAppUser.loading = true

          selectedUser = await this.getItem({ collection: 'user', id: user._id })
          selectedUser = selectedUser.data
       
          const menuRes = await this.get({ collection: 'role', query: { role: user.role } })
          

          if (menuRes && menuRes.data.docs.length)  {
            const { _id} = menuRes.data.docs[0]
              
            let role = await this.getItem({ collection: 'role', id: _id })
       
            const { menu, rights } = role.data
            
            selectedUser.menu = menu
            selectedUser.rights = rights
            
          }

        
          this.viewAppUser.loading = false
        }
        this.$store.commit('auth/viewUser', selectedUser)
      }
    } 
  }
}
</script>

