<template>
    <span v-html="text"></span>
</template>

<script>
export default {
    name: 'AutoCompleteCountryItem',
    computed: {
      text () {
       
      
        const { searchInput, itemText } = this.$attrs.parent
        const { [itemText]:name } = this.$attrs.item
        
        return searchInput ? this.maskValue(searchInput, name) : name
      }
    }
}
</script>

