 

import utility from './utility'
import dateforamt from 'dateformat' 
import { mapActions } from 'vuex'
import countries from './country'
import CountryFlag from 'vue-country-flag'

import config from './config'
console.log(config);
export default {
    install(Vue) {
        Vue.prototype.$getNestedValue = utility.getObjectValueByPath.bind(utility)
        Vue.mixin({
            components: {
                CountryFlag
            },
            methods: {
                ...mapActions({
                    get: 'api/get',
                    save: 'api/save',
                    update: 'api/update',
                    remove: 'api/remove',
                    getItem: 'api/getById',
                    logout: 'auth/logout',
                }),
                getFormatArgs(args, item) {
                    if (args)
                        args = args.map(el => {
                            if (typeof el == 'string' && el.substr(0, 1) == '$')
                                return this.$getNestedValue(item, el.substr(1, el.length))
                            else
                                return el
                        })
                    // console.log(args);
                    return args || []
                },
                maskValue(queryString, value) {
                    const openTag = '<span class="v-list-item__mask">'
                    const closeTag = '</span>'
                    const index = String(value).toLowerCase().indexOf(String(queryString).toLowerCase())
                    return [value.slice(0, index), openTag, value.slice(index, index + queryString.length), closeTag, value.slice(index + queryString.length)].join('')
                },
                getImageSrc(src) {
                    // const origin = `${location.protocol}//${location.hostname}`
                    return src ? (config.baseURL + src.replace('/','')) : '/img/blank-profile-picture.png'
                },
                getPermissions (collection) {
                    const { user } = this
                    if (user)
                        return user.rights.find(el => el.collection == collection) || false
                    else
                        return false
                }
            },
            computed: {
                user () {
                    
                    return this.$store.getters['auth/getUser'] 
                    // this.$store.state.auth.viewUser || this.$store.state.auth.user
                },
                userDepartment() {
                    if (!this.user) return ''
                    const { department } = this.user
                    return (department != null && department && department.name) ? `(${department.name})` : ''
                },
                permissions () {
                    return this.getPermissions(this.$route.params.collection)
                },
                canRead () {
                    return this.permissions ? this.permissions.read.value : false
                },
                canWrite() {
                    return this.permissions ? this.permissions.write.value : false
                },
                canUpdate() {
                    return this.permissions ? this.permissions.update.value : false
                },
                canDelete() {
                    return this.permissions ? this.permissions.delete.value : false
                },
                mainClass() {
                    return {
                        'grey lighten-4': !this.$vuetify.theme.dark,
                        'grey darken-3': this.$vuetify.theme.dark
                    }
                },
                isDark() {
                    return this.$vuetify.theme.dark
                },
            },
            
            filters: {
                dynamic (value, filterName, args) {
                    const [item, field] = value
                    const val = Vue.prototype.$getNestedValue(item, field)
                    
                    if (filterName) {
                        const filter = Vue.filter(filterName).bind({ item, field })
                        return filter(val, ...args)
                    }
                    else 
                        return val || '-'
                },
                uppercase(value) {
                    return value.toUpperCase()
                },
                country(value, flag = true, name = true, scale = 0.5) {
                    const country = countries.filter(e => e.code == value)
                    if (country.length) {
                        if (flag) {
                            return `<small class=" text-center d-flex" style="width:auto"><span class="flag f-${value.toLowerCase()} normal-flag" style="
                                        transform: scale(${scale});
                                        margin: -10px -15px;
                                    "></span>
                                    ${
                                        name ? `<span class="align-self-center">${country[0].name}</span>` : ''
                                    }
                                    
                                    
                                    </small>`
                        } else
                            return country[0].name
                    }
                    else
                        return value

                },
                countryName(value) {
                    const country = countries.find(e => e.code == value)
                    return country.name

                },
                moneyFormat(value, currency = 'EUR') {
                    return utility.money(value, currency)
                },
                money(value, currency = 'EUR', removeZero = false, __class = false) {
                    const { item } = this || { item:{} }
                   
                   
                   


                    const isZero = Number(value) == 0
                    const negative = Number(value) < 0
                    let _class = negative ? 'red--text' : 'green--text'

                    if (item.type == 'stk')
                        _class = 'orange--text'
                    

                    if (typeof __class == 'string')
                        _class = __class

                    if (removeZero && isZero) {
                        return '-'
                    } else
                        return `<span class="${_class}">${utility.money(value,  currency || item.currency)}</span>`
                },
                date(value, format = 'dd/mm/yyyy') {
                    return value ? dateforamt(value, format) : '-'
                },
                datetime(value, format = 'dd/mm/yyyy <b>HH:MM:ss</b>') {
                    return value ? dateforamt(value, format) : '-'
                },
                depositType(value) {
                  
                    let color = 'green'
                    let icon = 'mdi-bank-transfer-in'
                    switch (value) {
                        case 'wtd':
                            color = 'red'
                            icon = 'mdi-bank-transfer-out'
                            break;
                        case 'stk':
                            color = 'orange'
                            icon = 'mdi-bank-off'
                            break;
                        default:
                            color = 'green'
                            icon = 'mdi-bank-transfer-in'
                            break;
                    }
                    
                    return `<i aria-hidden="true" class="v-icon notranslate mdi ${icon}  ${color}--text"></i>`
                }
            }
        })
    }

}

